// React.
import React, { Component } from 'react';

// Route
import BrowserRouter from 'react-router-dom/BrowserRouter';

class RouteProvider extends Component {
  render() {
    const { children } = this.props;

    return (
      <BrowserRouter>
        { children }
      </BrowserRouter>
    );
  }
}

export default RouteProvider;



